.workwithus {
	padding: 120px 0;
	@include sm {
		padding: 50px 0; }
	&__titleinfo {
		margin-bottom: 30px; }
	&__title {
		margin-bottom: 38px; }
	&__row {
		@include sm {
			flex-direction: column; } }
	&__col {
		&:not(:last-child) {
			@include sm {
				margin-bottom: 20px; } } }
	&__btn {
		&:not(:last-child) {
			margin-right: 40px;
			@include sm {
				margin-right: 23px; } } }
	&__img {
		margin: -143px 0;
		display: flex;
		justify-content: flex-end;
		@include sm {
			margin: 30px 0 0;
			display: none; }
		img {
			margin-right: -71px;
			@include sm {
				margin: 0;
				max-width: 100%; } } } }
