  .article__buttons {
    margin-top: 20px;
  }
}

article__btn:not(:last-child),
.article__btn:not(:last-child) {
  margin-right: 40px;
}

@media only screen and (max-width: 767px) {
  article__btn:not(:last-child),
  .article__btn:not(:last-child) {
    margin-right: 24px;
  }
}

.img-hero {
  margin-right: -248px;
}

@media only screen and (max-width: 767px) {
  .img-hero {
    margin-right: 0;
  }
}

.imagelist {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
}

.imagelist li {
  margin-top: 20px;
}

@media only screen and (max-width: 767px) {
  .imagelist li {
    margin-top: 10px;
  }
}

.imagelist li:not(:last-child) {
  margin-right: 40px;
}

@media only screen and (max-width: 767px) {
  .imagelist li:not(:last-child) {
    margin-right: 24px;
  }
}

.footer {
  padding: 111px 0;
  font-size: 14px;
  line-height: 1.85;
}

@media only screen and (max-width: 1023px) {
  .footer {
    padding-top: 50px;
  }
}

@media only screen and (max-width: 767px) {
  .footer {
    padding: 50px 0;
  }
}

.footer__left {
  width: 33.4%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@media only screen and (max-width: 767px) {
  .footer__left {
    width: 100%;
  }
}

.footer__center {
  width: 50%;
}

@media only screen and (max-width: 767px) {
  .footer__center {
    display: none;
  }
}

.footer__right {
  width: 16.6%;
}

@media only screen and (max-width: 767px) {
  .footer__right {
    display: none;
  }
}

.footer__logo {
  margin-bottom: 35px;
  vertical-align: top;
  display: inline-block;
}

@media only screen and (max-width: 767px) {
  .footer__logo {
    margin-bottom: 20px;
  }
}

.footer__logo img {
  width: 160px;
  max-width: 100%;
  display: block;
}

.footer__description {
  margin-bottom: 25px;
  max-width: 264px;
  font-size: 12px;
  line-height: 1.9;
}

@media only screen and (max-width: 767px) {
  .footer__description {
    margin-bottom: 10px;
    max-width: 100%;
  }
}

.footer__email {
  color: #002DDC;
  -webkit-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
}

.no-touch .footer__email:hover {
  opacity: .8;
}

.footer__soc:not(:first-child) {
  margin-top: 41px;
}

.footer__copy {
  margin-top: 15px;
}

.footer__title {
  margin-bottom: 6px;
  font-size: 12px;
  font-weight: 900;
  line-height: 1.2;
  letter-spacing: 0.1em;
  color: #000;
}

.footer__list:not(:last-child) {
  margin-bottom: 33px;
}

.footer__center .footer__list {
  column-count: 2;
  column-gap: 66px;
}

@media only screen and (max-width: 1023px) {
  .footer__center .footer__list {
    column-gap: 30px;
  }
}

.footer__list li {
  padding: 4px 0;
}

.footer__list li.is-active a {
  color: rgba(0, 0, 0, 0.9);
}

.footer__list a {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.857;
  color: rgba(0, 0, 0, 0.5);
  -webkit-transition: color 0.3s ease-in-out;
  transition: color 0.3s ease-in-out;
}

.no-touch .footer__list a:hover {
  color: rgba(0, 0, 0, 0.9);
}
